.btn {
  position: relative;
  margin-bottom: 48px;
  overflow: visible;
  padding: 16px 24px;
  border: 0;
  outline: 0;
  background: transparent;
  color: #FFFFFF;
  font-size: 18px;
  letter-spacing: 0.5px;
  cursor: pointer;
  z-index: 1;
}

.btn::before {
  position: absolute;
  content: "";
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: #162E44;
  border-radius: 4px;
  z-index: -1;
  transition: all 0.3s ease-in-out;
}

.btn::after {
  position: absolute;
  content: "";
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  opacity: 0;
  border: 2px solid #162E44;
  border-radius: 4px;
  visibility: hidden;
  transition: all 0.2s ease 0s;
  transform: scale(1.05);
  will-change: transform;
  z-index: -1;
}

.btn:hover::after {
  opacity: 1;
  visibility: visible;
  transform: none;
  transition-duration: 0.3s;
  transition-delay: 0.1s;
}

.btn:hover::before {
  opacity: 0;
  visibility: hidden;
  transform: scale(0.9);
  transition-duration: 0.2s;
}

.btn:hover {
  background: transparent;
  color: #162E44;
}

