.header {
  background: #162E44;
}

.header .container .nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header .container .nav .nav__logo {
  padding: 8px 0;
}

.header .container .nav .nav__menu {
  display: flex;
  align-items: center;
  gap: 32px;
}

.header .container .nav .nav__menu .nav__list {
  list-style-type: none;
  display: flex;
  margin: 0;
  padding: 0;
}

.header .container .nav .nav__menu .nav__list .nav__item .nav__link {
  padding: 24px 16px;
  position: relative;
  display: block;
  font-size: 22px;
  font-weight: 500;
  color: #FFFFFF;
  text-decoration: none;
  text-shadow: -1px -1px 0px rgba(0, 0, 0, 0.3333333333);
  transition: all ease 0.35s;
}

.header .container .nav .nav__menu .nav__list .nav__item .nav__link::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  border-top: 3px solid #01B3A7;
  transform: translate3d(-100%, 0, 0);
  transition: all 0.35s cubic-bezier(0.05, 0.29, 0.11, 1.54), height 0.3s ease;
  visibility: hidden;
  opacity: 0;
}

.header .container .nav .nav__menu .nav__list .nav__item .nav__link:hover {
  color: #FFFFFF;
  background: rgba(255, 255, 255, 0.1098039216);
}

.header .container .nav .nav__menu .nav__list .nav__item .nav__link:hover::before {
  opacity: 1;
  visibility: visible;
  transform: translate3d(0, 0, 0);
}

.header .container .nav .nav__menu .nav__mode {
  cursor: pointer;
}

.header .container .nav .nav__menu .nav__mode .toggle-icon .moon {
  display: none;
}

.header .container .nav .nav__menu .burger__menu {
  display: none;
  flex-direction: column;
  justify-content: space-between;
  align-content: space-between;
  width: 22px;
  height: 14px;
}

.header .container .nav .nav__menu .burger__menu span {
  display: block;
  background: #FFFFFF;
  width: 100%;
  height: 2px;
  border-radius: 4px;
  transition: 0.3s;
}

.header .container .nav .nav__menu .burger__menu.active span:nth-child(1) {
  transform: rotate(-45deg) translate(1px, 6px);
}

.header .container .nav .nav__menu .burger__menu.active span:nth-child(2) {
  display: none;
}

.header .container .nav .nav__menu .burger__menu.active span:nth-child(3) {
  transform: rotate(45deg) translate(-3px, -10px);
}

.header .dropDown {
  width: 100%;
  padding: 0 0 32px;
  text-align: center;
  position: absolute;
  top: 74px;
  left: 0;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  opacity: 0;
  visibility: hidden;
  background: #FFFFFF;
  transition: 0.3s;
  z-index: 10;
}

.header .dropDown .dropDown-menu .dropDown-menu__list {
  display: block;
  text-align: center;
  width: 100%;
  height: 100%;
  z-index: 2;
}

.header .dropDown .dropDown-menu .dropDown-menu__list .dropDown-menu__item a {
  display: block;
  padding: 16px 0;
  color: #01B3A7;
  font-size: 22px;
  text-decoration: none;
  border-bottom: 2px solid #E1E1E1;
}

.header .dropDown.active {
  opacity: 1;
  visibility: visible;
  transition: 0.3s;
}

.header .header__content {
  padding: 48px 0 48px 172px;
